import { useEffect } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { Formik } from "formik";
import * as Yup from "yup";
import { InputBox, Buttons, SelectBox, TextareaBox } from "../../../components/RenderFroms";
import { useFetchSingle } from "../../../contexts/useFetchSingle";
import { usePost } from "../../../contexts/usePost";
import { usePatch } from "../../../contexts/usePatch";
import Breadcrumbs from "../../../components/Breadcrumbs";
import ListTitle from "../../../components/ListTitle";
const resource = "system_emails_templates";

const defaultValue = {
	systemAppId: "",
	systemSmtpServerId: "",
	systemEmailTemplateTitle: "",
	systemEmailTemplateSubject: "",
	systemEmailTemplateBody: "",
	systemEmailTemplateSenderName: "",
	systemEmailTemplateSenderEmail: "",
	systemEmailAction: "",
	systemEmailTemplateSaveLogs: ""

}

export default function AddEmailTemplate() {
	const navigate = useNavigate();
	const { id } = useParams();

	const validationSchemaInfo = Yup.object().shape({
		systemAppId: Yup.string().required("This is required"),
		systemEmailTemplateTitle: Yup.string().required("This is required"),
		systemEmailAction: Yup.string().required("This is required"),
		systemEmailTemplateSaveLogs: Yup.string().required("This is required"),
		systemEmailTemplateSubject: Yup.string().required("Subject is required"),
		systemEmailTemplateBody: Yup.string().required("Body is required"),
		systemEmailTemplateSenderName: Yup.string().required("Name is required"),
		systemEmailTemplateSenderEmail: Yup.string()
			.email("Invalid email address")
			.required("Email is Required"),

	});

	const { fetchSingle, data: users, loading: loadingData } = useFetchSingle();
	useEffect(() => {
		fetchSingle(resource, id)
	}, [id])

	const { create, data: respond, loading } = usePost();
	const { edit, data: respond1, loading: loading1 } = usePatch();
	const handleUpdate = (values: any) => {
		if (id) {
			edit(resource, values)
		} else {
			create(resource, values)
		}
	}

	useEffect(() => {
		if ((respond as any)?.status == 201) {
			navigate(-1)
		}
		if ((respond1 as any)?.status == 200) {
			navigate(-1)
		}
	}, [respond, respond1])

	
	const systemAppOptions = [{ label: 'Order Management System  (OMS)', value: '1' }, { label: 'Products and Price List Management System  (PPLMS)', value: '2' }]
	const saveLogOptions = [{ label: 'Yes, Save Logs of Outgoing Emails using this email template.', value: 'Yes' }, { label: 'No, Do Not Save Logs of Outgoing Emails using this email template.', value: 'No' }]
	const smtpserverOptions = [{ label: 'Instrowest Linux VPS', value: '1' }]

	if (loadingData) {
		return <>loading</>
	}
	
	return (
		<>
			<Breadcrumbs root="email-templates" pageName="Add Email Template" />


			<div className="main-content-body">
				<div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
					<div className="row">
						<div className="card">

							<ListTitle ListName={`${id ? 'Edit' : 'Add'} Email Template (All fields are mandatory).`} />

							<div className="form-horizontal has-validation-callback">
								<Formik
									initialValues={users ? users : defaultValue}
									validationSchema={validationSchemaInfo}
									onSubmit={(values: any) => handleUpdate(values)}
								>
									{({ handleChange, handleBlur, handleSubmit, values, errors }) => (
										<>
											<div className="card-body">

												<SelectBox
													required={true}
													options={systemAppOptions}
													name="systemAppId"
													lable="System App"
													placeholder="Select"
												/>


												<SelectBox
													required={true}
													options={smtpserverOptions}
													name="systemSmtpServerId"
													lable="SMTP Server"
													placeholder="Select"
												/>

												<InputBox
													required={true}
													name="systemEmailAction"
													lable="Email Template Action"
													placeholder=""

												/>

												<InputBox
													required={true}
													name="systemEmailTemplateTitle"
													lable="Email Template Title"
													placeholder=""

												/>

												<InputBox
													required={true}
													name="systemEmailTemplateSubject"
													lable="Email Template Subject"
													placeholder=""

												/>

												<div className="form-group">
													<label className="col-lg-4 col-md-4 col-sm-12 col-xs-12 control-label">&nbsp;</label>
													<div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
														<div className="red">Use the following variables into the email subject to set dynamic values.</div>
														<div className="grayBox">{`{Customer Name}`}</div>
													</div>
												</div>


												<TextareaBox
													required={true}
													name="systemEmailTemplateBody"
													lable="Email Template Body"
													placeholder=""

												/>

												<div className="form-group">
													<label className="col-lg-4 col-md-4 col-sm-12 col-xs-12 control-label">&nbsp;</label>
													<div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
														<div className="red">Use the following variables into the email body to set dynamic values.</div>
														<div className="grayBox">{`{Customer Name}`}</div>
													</div>
												</div>

												<InputBox
													required={true}
													name="systemEmailTemplateSenderName"
													lable="Sender's Name"
													placeholder=""

												/>
												<InputBox
													required={true}
													name="systemEmailTemplateSenderEmail"
													lable="Sender's Email ID"
													placeholder=""

												/>

												<SelectBox
													required={true}
													options={saveLogOptions}
													name="systemEmailTemplateSaveLogs"
													lable="Save Logs?"
													placeholder="Select"
												/>

												<div className="clear"></div>
											</div>
											<div className="card-action">
												<div className="form-group">
													<label className="col-lg-4 col-md-4 col-sm-12 col-xs-12 control-label">&nbsp;</label>
													<div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
														<Buttons loading={loading || loading1} className="btn-success" value="Save Email Template" onClick={() => handleSubmit()} />
														<Buttons className="btn-grey" value="Go Back to List" onClick={() => navigate(-1)} />
													</div>
												</div>
												<div className="clear"></div>
											</div>
										</>)}
								</Formik>
							</div>
						</div>
					</div>
				</div>
			</div>
		</>
	);
}
